#faq {
  @extend .article;
  background-color: $gray;

  .dropdown {
    position: relative;
    display: block;
    min-height: 54px;
    padding: 0 20px;
    font-size: 20px;
    border: 1px solid $grayDark;
    border-radius: 5px;
    background-color: $white;
    cursor: pointer;

    &:after {
      content: '+';
      position: absolute;
      top: 27px;
      right: 20px;
      transform: translateY(-50%);
      font-family: 'Arial';
    }

    &:not(:first-child) {
      margin-top: 15px;
    }

    .title {
      width: 100%;
      line-height: 54px;
    }

    &:not(.opened) .content {
      display: none;
    }

    .content {
      font-size: 16px;
      line-height: 1.6;
      border-top: 1px solid $grayLight;
      padding-top: 10px;
      margin-top: 10px;
      padding-bottom: 20px
    }
  }
}
